export default {
    props: {
        isChecked: {
            type: Boolean,
            default: true
        },
        titles: {
            type: [Array,Function],
            default() {
                return [
                    'components.common.choesCar',
                    'components.common.carList',
                    'components.common.Carattention',
                ] 
            }
        },
        vehilceApi: {
            default: '/basic/tree/findVehicleTreeInfoList'
        },
        listsApi: {
            default: '/basic/tree/getVehicleListByPlate'
        },
        attentionApi: {
            type: String,
            default: '/basic/vehicle/findVehicleAttentionInfoList'
        },
        // 
        nodeMonitor: Function,
        // 
        treeData: Array,
        // 
        storeData: {
            type: Object,
            default() {return {} }
        },
        // 
        vehicleSearch: {
            default: ''
        }
    },
    data() {
        var _vm = this;
        return {
            tree: [],
            isNeedLoad: false,
            store: {
                // {id, id, id}
                vehicles: {},
                // [id, id, ...]
                vehiclesArray: [],
                // 
                getLists(params) {
                    return _vm.$http({
                        url: _vm.listsApi,
                        params
                    })
                },
                // id
                addOrRemoveVehicles(mess) {
                    const {name, data} = mess;

                    typeof _vm.nodeMonitor === 'function'&& _vm.nodeMonitor(mess);
                    switch(name) {
                        case 'check':
                            data.forEach(val => {
                                this.vehicles[val.id] = val;
                                _vm.$refs['tree'].reference().setCheckedKeys([val.id], true);
                            });
                        break;
                        case 'cancel':
                            data.forEach(val => {
                                _vm.$refs['tree'].reference().setCheckedKeys([val.id], false);
                                delete this.vehicles[val.id];
                            });
                        break;
                    };
                    //  {id, id} ==> [id, id]
                    this.vehiclesArray = _vm._ObjectTransferArray(this.vehicles);
                }
            }
        }
    },
    provide() {
        return {
            store: this.store
        }
    },
    watch: {
        // 
        treeData: {
            immediate: true,
            handler(val) {
                if(Array.isArray(val) && val.length > 0) {
                    this.tree = val;
                } else {
                    this.tree = [];
                    // 
                    //this.isNeedLoad = true;
                }
            }
        },
        // id
        storeData: {
            immediate: true,
            handler(val) {
                this.store.vehicles = val;
                this.store.vehiclesArray = this._ObjectTransferArray(val);
            }
        }
    },
    methods: {
        /**
         * 
         * @param {Object} obj
         */
        _ObjectTransferArray(obj) {
            var results = [];
            //{isArray:[id,id,id....]}
            if (obj.isArray) {
                results= obj.isArray
            }else{
            //{[],[],[]...}
                 Object.keys(obj).forEach(val => {
                     results.push(val);
                 });
            }
           
            return results;
        }
    }
}